import { Component, OnInit, ViewChild } from '@angular/core';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { ActivationEnd, Router, ActivatedRoute } from '@angular/router';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { PedidosService } from '../../../providers/Api/Pedidos.service';


@Component({
  selector: 'app-citas',
  templateUrl: './citas.page.html',
  styleUrls: ['./citas.page.scss'],
})
export class CitasPage implements OnInit {

  
  constructor(

    private almacenamiento: InicioService,
    private router: Router,
    private active: ActivatedRoute,
    private toast: ToastServices,
    private apiPedido: PedidosService
  ) { 

  

  }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.crear();
  }

  async crear() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

      this.apiPedido.consultarChat(info.token, {id: this.active.snapshot.paramMap.get('id')}).subscribe((data: any) => {
        // console.log(data);
        if (data.exe) {


        this.almacenamiento.insertarChat(data.response[0]).then(() => {
          this.router.navigate(['/central/chats/chat']);
        }).catch(() => {
          this.router.navigate(['/central/menu']);
          this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
        })
       
        } else {
          this.toast.toastError('No se pudo traer la información, inténtelo más tarde')
        }
      }, (err) => {
        this.toast.toastError('No se pudo traer la información, inténtelo más tarde')
      })

    }
  }



}
